.text-field {
  $root: &;

  width: 320px;

  &__header {
    display: flex;
    justify-content: space-between;
  }

  &__label {
    font-size: 12px;
    line-height: 15px;
    font-weight: bold;
    text-transform: uppercase;
    margin: 0 0 5px;

    &:nth-child(2) {
      font-weight: normal;
      color: rgb(31 32 65 / 50%);
    }
  }

  &__wrapper {
    position: relative;
  }

  &__input {
    font-size: 14px;
    line-height: 24px;
    color: rgb(31 32 65 / 75%);
    font-family: montserrat, Arial, sans-serif;
    box-sizing: border-box;
    width: 100%;
    height: 44px;
    margin: 0;
    padding: 0 14px;
    border: 1px solid rgb(31 32 65 / 25%);
    border-radius: 4px;
    outline: 0;

    &::placeholder {
      font-family: montserrat, Arial, sans-serif;
      color: rgb(31 32 65 / 25%);
    }

    &:focus {
      border: 1px solid rgb(31 32 65 / 50%);
    }

    &:hover {
      border: 1px solid rgb(31 32 65 / 50%);
    }
  }

  &_type_date {
    #{$root}__input {
      position: absolute;
      background: transparent;
      color: rgb(31 32 65 / 50%);
      z-index: 1;

      &_double {
        background: white;
        border: 1px solid transparent;
        z-index: 0;
      }

      &_invalid {
        color: red;
      }
    }
  }

  &:hover {
    #{$root}__input_double {
      border: 1px solid transparent;
    }
  }

  &__date {
    display: none;
  }
}

@media (max-width: 400px) {
  .text-field {
    width: 266px;
  }
}
